body {
  margin: 0;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

::-webkit-scrollbar {
  width: 8px; 
  height: 8px; 
  background-color: rgba(0, 0, 0, 0.281);
  border-radius: 9em;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(109, 109, 109, 0.733);
  border-radius: 9em;
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgba(146, 146, 146, 0.733);
}
.newTask{
    position: fixed;
    width: 60px;
    height: 60px;
    background-color: #D103FC;
    bottom: 5%;
    right: 5%;
    border-radius: 100%;
    box-shadow: 0px 0px 10px #d203fc86, 0px 0px 15px #d203fc86;
    display: flex;
    justify-self: center;
    align-items: center;
    cursor: pointer;
}

.newTask span{
    position: relative;
}

.newTask span::after{
    content: '';
    position: absolute;
    width: 20px;
    height: 3px;
    background-color: #fff;
    left: 20px;
}

.newTask span::before{
    content: '';
    position: absolute;
    width: 3px;
    height: 20px;
    background-color: #fff;
    left: 28px;
    top: -9px;
}



